<script>
    import { 
        Col, 
        Container, 
        Row,
        Card,
        CardBody,
        CardFooter,
        CardHeader,
        CardSubtitle,
        CardTitle,
        Icon,
        CardText,
    } from 'sveltestrap';
    import { onMount, afterUpdate } from 'svelte';
    import { fly, fade, slide } from 'svelte/transition';
    import moment from 'moment';
    import * as echarts from 'echarts/core';
    import { BarChart, LineChart, PieChart } from 'echarts/charts';
    import { TitleComponent, TooltipComponent, GridComponent, LegendComponent } from 'echarts/components';
    import { CanvasRenderer } from 'echarts/renderers';



    export let baseUrl;

    // 初始fetch
    let about = {};
    onMount(async () => {
        let data = await fetch(`${baseUrl}/about/index.json`).then(data => data.json());
        about = await data.result;
        setCharts(about.charts);
    });



    function sleep(ms) {
		return new Promise( resolve => setTimeout(resolve, ms));
	}
    

    //in的時候延遲
    function fade_d(node, {
	    delay = 500,
	    duration = 200
    }) {
	    const o = +getComputedStyle(node).opacity;
	    return {
		    delay,
		    duration,
		    css: t => `opacity: ${t * o}`
	    };
    }


    // echart相關試做
    echarts.use(
        [TitleComponent, TooltipComponent, GridComponent, LegendComponent, BarChart, PieChart, LineChart, CanvasRenderer]
    );


    function setActivityArea(x, series) {
        let myChart = echarts.init(document.getElementById('a'), 'dark');
        myChart.setOption({
            title: {
                text: 'Monthly Activity'
            },
            tooltip: {
                trigger: 'item'
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: x,
                axisLabel: {  
                    //interval: 0,
                    //rotate:25,
                    textStyle:{ 
                        color: '#eef1fa'
                    },                 
                },  
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                data: series,
                type: 'line',
                areaStyle: {
                    color: '#ee6666'
                },
                itemStyle: {
                    color: '#ee6666'
                }
            }],
            backgroundColor: 'transparent'
        });
    }


    function setCategoryBar(x, series) {
        let myChart = echarts.init(document.getElementById('c'), 'dark');

        myChart.setOption({
            title: {
                text: 'Categories'
            },
            tooltip: {
                trigger: 'item'
            },
            xAxis: {
                data: x,
                axisLabel: {  
                    interval: 0,
                    rotate:25,
                    textStyle:{ 
                        color: '#eef1fa'
                    },                 
                },  
            },
            yAxis: {},
            series: [{
                type: 'bar',
                data: series  
            }],
            backgroundColor: 'transparent',
            
        });
    }


    function setTagPie(data) {
        let myChart = echarts.init(document.getElementById('t'), 'dark');

        myChart.setOption({
            title: {
                text: 'Tags'
            },

            tooltip: {
                trigger: 'item'
            },
            legend: {
                bottom: 0,
                left: 'center',
                textStyle: {
                    color: '#eef1fa'
                }
            },
            series: [{
                type: 'pie',
                radius: '50%',
                data: data,
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                },
                label: {
                    color: '#eef1fa',
                    textBorderColor: '#eef1fa'
                }

            }],
            backgroundColor: 'transparent'
        });
    }


    function setCharts(chartsData) {
        let categorySeries = chartsData.categories_data.series.sort((a, b) => a.name > b.name);
        let categoriesBar = {
            x: categorySeries.map((el => el.name)),
            series:categorySeries.map((el => el.value))
        };
        setCategoryBar(categoriesBar.x, categoriesBar.series);
        
        let tagPie = {
            legend: chartsData.tags_data.legend,
            seledted: chartsData.tags_data.selected,
            data: chartsData.tags_data.series
        };
        setTagPie(tagPie.data, tagPie.legend, tagPie.selected);

        let activityArea = {
            x: [],
            series: []
        };
        for (let i = 0; i < 240; i++) {
            let month = moment().subtract(i, 'M').format('YYYY-MM');
            activityArea.x.push(month);
            activityArea.series.push(chartsData.monthly_count[month] || 0);
            if (month === Object.keys(chartsData.monthly_count)[0]) break;
        }
        setActivityArea(activityArea.x.reverse(), activityArea.series.reverse());
    }
</script>


<Container class="mt-3">
    
    <Row>
        <Col xs="12" sm="12" md="12" lg="12">

            <div class="post-card mb-2"  in:fade_d out:fade>
                <Card>
                    <CardHeader>
                        <CardTitle>
                            About
                        </CardTitle>
                    </CardHeader>
                    <CardBody>
                        <CardText>
                            {@html about.content}
                        </CardText>
                        
                    </CardBody>
                </Card>
            </div>
 
        </Col>
        <Col xs="12" sm="12" md="12" lg="12">

            <div class="post-card mb-2"  in:fade_d out:fade>
                <Card>
                    <CardHeader>
                        <CardTitle>
                            Statistics
                        </CardTitle>
                    </CardHeader>
                    <CardBody>
                        <Row>
                            <Col xs="12" sm="12" md="12" lg="4">
                                <div id="a" style="width: 100%; height: 320px"></div>
                            </Col>
                            <Col xs="12" sm="12" md="12" lg="4">
                                <div id="c" style="width: 100%; height: 320px"></div>
                            </Col>
                            <Col xs="12" sm="12" md="12" lg="4">
                                <div id="t" style="width: 100%; height: 320px"></div>
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            </div>
 
        </Col>


    </Row>
</Container>



<style>
    .post-card :global(.card .card-header .card-title) {
        margin-bottom: 0px;
    }
</style>